.featured--title {
  font-size: 2em;
  text-align: center;
}

.featured--card--container {
  display: flex;
  justify-content: space-around;
  /* justify-content: flex-start; */
  align-items: center;
}

.featured--card {
  width: 500px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.featured--card--image {
  width: 400px;
  height: auto;
}

.featured--card--text--title {
  font-size: 1.5em;
  text-align: center;
}

.featured--card--text--description {
  width: 400px;
  text-align: start;
}
